<template>
  <div class="contact">
    <h2>
      Contact Us
    </h2>
    <h4 v-if="successMessage" class="confmessage green">
      <v-icon>thumb_up</v-icon> {{ successMessage }}
    </h4>
    <h4 v-if="errorMessage" class="confmessage red">
      <v-icon>thumb_down</v-icon> {{ errorMessage }}
    </h4>
    <v-card panel class="blue-grey darken-2 no-top">
        <span class="white-text">
          <v-icon tiny>info</v-icon> You are more than welcome to contact us directly at
          <a mailto="ubergeekreview@gmail.com">ubergeekreview@gmail.com</a>. However, feel
          free to save your fingers a few strokes by using the form below.
        </span>
    </v-card>
    <form class="col s12">
      <div class="input-field">
        <v-text-input name="subject" v-model="subject" id="subject" class="validate" type="text"></v-text-input>
        <label for="subject">
          Subject
        </label>
      </div>
      <div class="input-field">
        <v-text-area name="message" v-model="message" id="message" class="validate"></v-text-area>
        <label for="message">
          Message
        </label>
      </div>
      <div class="row">
        <v-btn large waves-effect><span v-on:click="sendMessage">Send Away!</span></v-btn>
      </div>
    </form>
  </div>
</template>

<script>
  import * as services from '../services';
  export default {
    name: 'contact',
    data () {
      return {
        subject: '',
        message: '',
        successMessage: '',
        errorMessage: ''
      }
    },
    methods: {
      sendMessage() {
        if (!this.subject || !this.message) {
          window.Materialize.toast('You must enter all information', 4000);
        } else {
          services.app.authenticate().then(result => {
            console.log('auth result', result.data);
            let data = result.data;
            let mailObj = {
              from: data.email,
              to: 'ubergeekreview@gmail.com',
              subject: this.subject,
              html: this.message
            };
            console.log('mail', mailObj);
            services.mailService.create(mailObj)
              .then(this.subject = '')
              .then(this.message = '')
              .then(this.successMessage = 'Thank you for contacting us! We will get back to you as soon as posisble.')
              .catch(err => {
                if (!err) {
                  this.errorMessage = '';
                }
                this.successMessage = '';
                this.errorMessage = err;
              });
          });
        }
      }
    }
  }
</script>

<style>
  .confmessage {
    padding: 20px;
  }
</style>


