<template>
  <div class="services">
    <h2>
      About
    </h2>
    <div class="col s12">
      <div class="row">
        <h4>&hellip;Us</h4>
        <p>
          There are a few apparent truths about UberGeek Reviews in that
          <ul>
            <li>
              We are geeks
            </li>
            <li>
              We like the word "uber."
            </li>
            <li>
              We read a ton of books.
            </li>
            <li>
              We are passionate about sharing the books we love.
            </li>
          </ul>
          That's it. If you came looking for information about stakeholders or corporate mottos, then we simply don't
          have that. Yes, we know you are sad. Apologies to all involved.
        </p>
      </div>
      <div class="row">
        <h4>&hellip;the Reviews</h4>
        <p>
          We obviously don't take talking about ourselves seriously, but the same cannot be said about our
          review process. For each book that we review, we do a detailed rating on a few different factors:
        </p>
        <ul>
          <li>
            <p>
              <strong>Grammar / Writing Style</strong>: These items go at the top because they <em>belongs</em>
              there for a few reasons. First, an author's medium is words. Without the ability to structure an
              idea into a cognizant format, then everyone loses. Second, poor grammar and writing style will jerk
              a reader out of the experience faster than the lastest horrid incarnation of <em>I Am Legend</em> can
              ruin a fantastic book. For example, say an author forgets to take away the ending quotation mark
              at the end of the paragraph when the next paragraph continues the same character's dialogue.
              Now the reader will have to keep checking to determine who is speaking based on contextual
              clues rather than a simple visual check.
            </p>
            <p>
              As for writing style, let's go over one case in point: the ambiguous "it."
              I have downloaded countless samples, encountered five ambiguous it situations in the first few pages,
              and immediately removed the book from my device.  This style is lazy, impedes the reader, and
              disrupts the story. If an author is too lazy to actually clarify what that "it" means, then
              he or she has written a work I will not read.
            </p>
            <p>
              At the end of the day, UGR is not advocating that a book has to be grammatically perfect to read.
              Even novels that come out from huge publication houses have mistakes. Yet there is a huge difference
              between predictable mistakes and quirks and sheer laziness.
            </p>
          </li>
          <li>
            <strong>Story</strong>: This category covers a few elements. How was the overall story? Did the book
            depend upon plot twists or surprises that were too easy to predict? How was the pacing of the story? Were
            treasure trolls ritualistically sacrificed? How was the overall complexity? These are a few of the questions
            that we will explore.
          </li>
          <li>
            <strong>Character Development</strong>: No one enjoys reading about mannequins who are purely two dimensional
            in nature or characters who are immeasurably powerful from the get go. This section will examine how the
            characters progressed, grew, loved, cried, or became enraged throughout the novel.
          </li>
          <li>
            <strong>Genre Enhancements</strong>: The interesting section! Not every review will contain this part.
            If a novel introduces a truly novel idea about how the rules of that genre work, then that will be called
            out here.
          </li>
          <li>
            <strong>Similar Works</strong>: Want a cross-reference as to what kind of tropes the book in question
            uses so you can determine what books are similar to it? Then come no further.
          </li>
          <li>
            <strong>Format</strong>: If there are any noticable issues with the digital copies of a novel, then they
            will be spelled out herein so the reader can make an informed decision.
          </li>
        </ul>
        <p>
          All reviews will be spoiler free. If that rule is ever broken, then we'll provide PLENTY of warning.
        </p>
      </div>
    </div>
