<template>
  <div class="hello">
    <h1>
      Welcome to <span class="uber">UberGeek</span> <span class="reviews">Reviews</span>!
    </h1>
    <v-card panel class="blue-grey darken-2 no-top">
        <span class="white-text">
            <v-icon tiny>info</v-icon> UGR is a site dedicated to reviewing all manners of independently published novels so that you don't need
            to swim through a sea of kruft to find something amazing to read.
        </span>
    </v-card>
    <h2>Latest Reviews</h2>
    <div class="row">
      <v-reviews number="10"></v-reviews>
      <!--
      <div v-for="review in reviews" class="col s6">
        <div class="card horizontal">
          <div class="card-image">
            <img :src="getSidebar(review.topic)">
          </div>
          <div class="card-stacked">
            <div class="card-content">
              <h4><a :href="reviewLink(review.topic, review._id)">{{review.title}}</a></h4>
              <p class="truncate" v-html="review.article"></p>
            </div>
          </div>
        </div>
      </div>
      -->
    </div>
  </div>
</template>

<script>
import * as services from '../services';
import ReviewView from './ReviewView.vue'
export default {
  name: 'hello',
  components: {
    'v-reviews': ReviewView
  },
  data () {
    return {
      reviews: []
    }
  },
  created() {
    this.loadReviews();
  },
  methods: {
    loadReviews() {
      services.reviewService.find({
        query: {
          $sort: {createdAt: -1},
          $skip: this.skip
        }
      })
        .then(result => {
          console.log('Fetched', result.data);
          this.reviews = result.data;
          this.total = result.total;
          this.limit = result.limit;
          this.skip = result.skip;
          this.loading = false;
          if (this.total > this.limit) {
            this.pagControls = true;
            this.pagLength = (this.total / this.limit);
          }
          return this.reviews;
        })
        .catch(err => {
          console.log('ERR', err);
          this.error = err;
        });
    },
    getSidebar(value) {
      let imgs = require.context('../assets/img/sidebars', false, /\.png$/);
      return imgs('./' + value + '-sidebar.png');
    },
    reviewLink(topic, id) {
      return '/topic/' + topic + '/' + id;
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
    margin-bottom: 0;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }
  li {
    display: inline-block;
    margin: 0 10px;
  }
  a {
    color: #42b983;
  }
  .uber {
    font-family: 'VT323', monospace;
    font-size: 5rem;
  }
  .reviews {
    font-family: 'Special Elite', cursive;
    font-size: 4rem;
  }
  .no-top {
    margin-top: 0 !important;
  }
</style>



