import Vue from 'vue';
import router from './router';
import Materials from 'vue-materials';
import Filters from 'vue2-filters';
import App from './components/App';

Vue.use(Materials);
Vue.use(Filters);

/* eslint-disable no-new */
new Vue({
  router,
  ...App
}).$mount('#app');



