import Vue from 'vue';
import Router from 'vue-router';
import Signup from '../components/Signup.vue';
import Login from '../components/Login.vue';
import Home from '../components/Home.vue';
import Topic from '../components/Topic.vue';
import Review from '../components/Review.vue';
import Contact from '../components/Contact.vue';
import Services from '../components/Services.vue';
import Media from '../components/Media.vue';
import About from '../components/About.vue';
import PostReview from '../components/admin/PostReview.vue';
import * as services from '../services';

Vue.use(Router);

function requireAuth(to, from, next) {
  if (window.localStorage.getItem('feathers-jwt') === null) {
    next({
      path: '/login',
      query: {
        redirect: to.fullPath
      }
    })
  } else {
    next();
  }
}

function requireAdmin(to, from, next) {
  services.app.authenticate().then(result => {
    console.log('auth result', result.data);
    if (result.data.roles && result.data.roles.indexOf('admin') > -1) {
      next();
    } else {
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      });
    }
  })
    .catch(err => {
      window.Materialize.toast('You do not have sufficient privileges: ' + err, 4000);
    });
}

export default new Router({
  mode: 'history',
  scrollBehavior: () => ({
    y: 0
  }),
  routes: [
    {
      path: '/',
      component: Home
    }, {
      path: '/signup',
      component: Signup
    }, {
      path: '/login',
      component: Login
    }, {
      path: '/logout',
      beforeEnter(to, from, next) {
        services.app.logout();
        next('/login')
      }
    }, {
      path: '/topic/:id',
      component: Topic
    }, {
      path: '/topic/:topic/:id',
      component: Review
    }, {
      path: '/admin/add-review',
      component: PostReview,
      beforeEnter: requireAdmin
    }, {
      path: '/contact',
      component: Contact,
      beforeEnter: requireAuth
    }, {
      path: '/services',
      component: Services
    }, {
      path: '/media',
      component: Media
    }, {
      path: '/about',
      component: About
    }
  ]
});



