<template>
  <div class="row">
    <div class="loading center-align" v-if="loading">
      <v-progress-circular active large></v-progress-circular>
    </div>
    <div class="error" v-if="error">
      {{ error }}
    </div>
    <div class="row">
      <div v-for="review in reviews" class="col s6">
        <div class="card horizontal">
          <div class="card-image">
            <img :src="getSidebar(review.topic)">
          </div>
          <div class="card-stacked">
            <div class="card-content">
              <h4><a :href="reviewLink(review.topic, review._id)">{{review.title}}</a></h4>
              <p class="truncate" v-html="getArticleContent(review.article)"></p>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="row">
      <v-pagination color="blue" v-if="pagControls" :length="pagLength" v-model="active" class="center-align" v-on:input="setPage($event)"></v-pagination>
    </div>
  </div>
</template>

<script>
  import * as services from '../services';
  const striptags = require('striptags');
  export default {
    name: 'reviewView',
    props: ['number', 'topic'],
    data () {
      return {
        reviews: [],
        limit: '',
        skip: '',
        total: '',
        loading: true,
        error: false,
        pagControls: false,
        pagLength: '',
        active: 1,
        reviewQuery: {
          query: {
            $sort: {createdAt: -1},
            $skip: this.skip
          }
        }
      }
    },
    created() {
      this.loadReviews();
    },
    watch: {
      $route (to, from) {
        this.loadReviews();
      }
    },
    methods: {
      loadReviews() {
        if (this.topic && this.topic !== '') {
          this.reviewQuery.query.topic = this.topic;
        }
        if (this.number && this.number !== '') {
          this.reviewQuery.query.$limit = this.number;
        }
        services.reviewService.find(this.reviewQuery)
          .then(result => {
            this.reviews = result.data;
            this.total = result.total;
            this.limit = result.limit;
            this.skip = result.skip;
            this.loading = false;
            if (this.total > this.limit) {
              this.pagControls = true;
              this.pagLength = Math.round(this.total / this.limit);
            }
            return this.reviews;
          })
          .catch(err => {
            console.log('ERR', err);
            this.error = err;
          });
      },
      getSidebar(value) {
        let imgs = require.context('../assets/img/sidebars', false, /\.png$/);
        return imgs('./' + value + '-sidebar.png');
      },
      reviewLink(topic, id) {
        return '/topic/' + topic + '/' + id;
      },
      getArticleContent(value) {
        let cutoff = 65;
        let html = value;
        let fulltext = striptags(html);
        if (fulltext.length <= cutoff) {
          return fulltext;
        }
        let subString = fulltext.substr(0, cutoff - 1);
        return subString.substr(0, subString.lastIndexOf(' ')) + '&hellip;';
      },
      setPage(page) {
        this.loading = true;
        let perPage = this.number;
        this.active = page;
        this.skip = (page * perPage) - page;
        this.reviewQuery.query.$skip = this.skip;
        this.loadReviews();
      }
    }
  }
</script>


