<template>
  <div class="global-nav">
    <nav class="nav-extended">
      <v-nav fixed nav-class="nav-wrapper grey darken-3">
        <a href="/" class="brand-logo" slot="logo">
          <span class="uber">UberGeek</span>
          <span class="reviews">Reviews</span>
        </a>
        <a v-side-nav:demo="nav" class="hide-on-med-and-up"><v-icon>view_headline</v-icon></a>
        <v-side-nav id="demo">
          <ul>
            <li>
              <router-link to="/media">Media</router-link>
            </li>
            <li>
              <router-link to="/services">Services</router-link>
            </li>
            <li>
              <router-link to="/contact">Contact Us</router-link>
            </li>
            <li>
              <router-link to="/about">About</router-link>
            </li>
            <li v-if="loggedIn">
              <router-link to="/logout">Log out</router-link>
            </li>
            <li v-if="!loggedIn">
              <router-link to="/login">Log In</router-link>
            </li>
            <li v-if="!loggedIn">
              <router-link to="/signup">Sign Up</router-link>
            </li>
          </ul>
        </v-side-nav>
        <ul class="right hide-on-small-only"">
          <li>
            <router-link to="/media">Media</router-link>
          </li>
          <li>
            <router-link to="/services">Services</router-link>
          </li>
          <li>
            <router-link to="/contact">Contact Us</router-link>
          </li>
          <li>
            <router-link to="/about">About</router-link>
          </li>
          <li v-if="loggedIn">
            <router-link to="/logout">Log out</router-link>
          </li>
          <li v-if="!loggedIn">
            <router-link to="/login">Log In</router-link>
          </li>
          <li v-if="!loggedIn">
            <router-link to="/signup">Sign Up</router-link>
          </li>
        </ul>
      </v-nav>
      <div class="nav-content">
        <ul class="tabs tabs-transparent">
          <li v-for="item in items" class="tab">
            <router-link :to="item.link">
              <span :class="'icon-' + item.title.toLowerCase()"></span>{{ item.title }}
            </router-link>
          </li>
        </ul>
      </div>
    </nav>
  </div>
</template>

<script>
  export default {
    name: 'globalNav',
    data () {
      return {
        loggedIn: !!window.localStorage.getItem('feathers-jwt'),
        nav: {
          edge: 'left'
        },
        items: this.$parent.items
      }
    },
    watch: {
      '$route': 'checkAuth'
    },
    methods: {
      checkAuth() {
        this.loggedIn = !!window.localStorage.getItem('feathers-jwt')
      }
    }
  }
</script>

<style src="../assets/css/ugr.css"></style>
<style scoped>
  .brand-logo {
    margin-left: 20px;
  }
  .uber {
    font-family: 'VT323', monospace;
  }
  .reviews {
    font-family: 'Special Elite', cursive;
    font-size: 1.8rem;
  }
  .nav-content {
    background-color: #ccc;
  }
  .nav-content .tabs .tab {
    background-color: #ccc;
  }
  .nav-content .tabs .tab .active {
    background-color: #ccc;
  }
  a.active {
    background-color: #ccc !important;
  }
  .nav-content .tabs .tab a {
    color: #1565C0;
    font-weight: bolder;
  }
  .nav-content .tabs .tab .router-link-active {
    background-color: #1565C0;
    color: #fff;
    font-weight: bolder;
  }
</style>



