<template>
  <div class="login">
    <h2>
      Login
    </h2>
    <p v-if="$route.query.redirect">
      You need to login first
    </p>

    <p v-if="error" class="error">
      Incorrect email or password
    </p>

    <form class="col s12">
      <div class="input-field">
        <v-icon prefix>account_circle</v-icon>
        <v-text-input name="email" v-model="email" id="email"></v-text-input>
        <label for="email">
          Email Address
        </label>
      </div>
      <div class="input-field">
        <v-icon prefix>https</v-icon>
        <v-text-input name="password" v-model="password" id="password" type="password"></v-text-input>
        <label for="password">
          Password
        </label>
      </div>
      <div class="row">
        <v-btn large waves-effect><span v-on:click="login">Login</span></v-btn>
      </div>
    </form>
  </div>
</template>

<script>
  import * as services from '../services';
  export default {
    data() {
      return {
        email: '',
        password: '',
        error: false
      }
    },
    methods: {
      login() {
        services.app.authenticate({
          type: 'local',
          'email': this.email,
          'password': this.password
        })
          .then(this.loginSuccess)
          .catch(this.loginError)
      },
      loginSuccess() {
        this.$router.replace(this.$route.query.redirect || '/');
      },
      loginError() {
        this.error = true;
      }
    }
  }
</script>

<style>
  .error {
    color: red;
  }
  .login {
    min-height: 800px;
    padding: 50px;
  }
  h2 {
    margin-bottom: 4rem !important;
  }
</style>



