<template>
  <div class="post-review">
    <h2>
      Add a Review
    </h2>
    <form>
      <div class="input-field">
        <v-text-input name="title" id="title" v-model="title"></v-text-input>
        <label for="first">Title</label>
      </div>
      <div class="input-field">
        <v-select name="topic"
                  id="topic"
                  v-model="topic"
                  :items="topics"
        ></v-select>
        <label for="select">Topic</label>
      </div>
      <div class="input-field">
        <v-text-input name="videoLink"
                      id="videoLink"
                      v-model="videoLink"
        ></v-text-input>
        <label for="first">Video Link</label>
      </div>
      <label for="input-field">
        Main Image
      </label>
      <div class="input-field">
        <v-text-input name="mainImage" id="mainImage" v-model="mainImage"></v-text-input>
        <label for="mainImage">Main Image</label>
      </div>
      <label for="vue-editor">
        Review
      </label>
      <vue-editor name="article" :show-preview="true" :use-save-button="false" @editor-updated="setContent"></vue-editor>
      <div class="row">
        <v-btn large waves-effect><span v-on:click="addReview">Add Review</span></v-btn>
      </div>
    </form>
  </div>
</template>

<script>
  import {VueEditor} from 'vue2-editor';
  import * as services from '../../services';

  /*
  Dropzone.options.myAwesomeDropzone = {
    init: function() {
      this.on('addedfile', function(file) {
        console.log('FILE', file);
        // this.$dialog('Added file.');
        services.app.authenticate()
          .then(
            services.uploadService.create({
              file: file
            })
              .then(res => {
                console.log('RES', res);
              })
              .catch(err => {
                console.log('err', err);
              })
          )
      });
    }
  };
  */

  export default {
    name: 'review',
    data () {
      return {
        topic: '',
        title: '',
        article: '',
        author: '',
        videoLink: '',
        mainImage: '',
        review: '',
        error: '',
        topics: [
          {
            id: 'anime',
            text: 'Anime'
          }, {
            id: 'tech',
            text: 'Tech'
          }, {
            id: 'zombies',
            text: 'Zombies'
          }, {
            id: 'horror',
            text: 'Horror'
          }, {
            id: 'fantasy',
            text: 'Fantasy'
          }, {
            id: 'scifi',
            text: 'Scifi'
          }, {
            id: 'superheroes',
            text: 'Superheroes'
          }, {
            id: 'games',
            text: 'Games'
          }, {
            id: 'litrpg',
            text: 'LitRPG'
          }
        ]
      }
    },
    components: {
      VueEditor
    },
    methods: {
      setContent(value) {
        this.article = value;
      },
      addReview() {
        console.log('adding')
        services.app.authenticate().then(result => {
          console.log('auth result', result.data);
          let reviewObj = {
            topic: this.topic,
            title: this.title,
            article: this.article,
            author: result.data._id,
            videoLink: this.videoLink,
            mainImage: this.mainImage
          };
          console.log('Obj', reviewObj);
          if (result.data._id) {
            services.reviewService.create(reviewObj)
              .then(this.topic = '')
              .then(this.title = '')
              .then(this.article = '')
              .then(this.author = '')
              .then(this.videoLink = '')
              .then(this.mainImage = '')
              .then(this.successMessage = 'Article created!')
              .catch(function (err) {
                console.log('Uh oh, something bad happened!', err);
              })
          }
        })
      }
    }
  }
</script>

<style scoped>
  .post-review {
    margin: 100px 60px;
  }
</style>


