import io from 'socket.io-client';
import feathers from 'feathers/client';
import hooks from 'feathers-hooks';
import socketio from 'feathers-socketio/client';
import authentication from 'feathers-authentication/client';

const socket = io('http://localhost:4040/');

export const app = feathers()
  .configure(socketio(socket))
  .configure(hooks())
  .configure(authentication({storage: window.localStorage}));

socket.on('reconnect', () => { app.authenticate(); });

export const userService = app.service('users');
export const reviewService = app.service('reviews');
export const uploadService = app.service('uploads');
export const mailService = app.service('mailers');



