<template>
  <div class="review">
    <div class="loading center-align" v-if="loading">
      <v-progress-circular active large></v-progress-circular>
    </div>
    <div class="error" v-if="error">
      {{ error }}
    </div>
    <h2>{{ title }}</h2>
    <div class="row center-align" v-if="mainImage">
      <v-material-box :src="getPic(mainImage)" width="50%"></v-material-box>
    </div>
    <div class="row center-align" v-if="videoLink">
      <div class="video-container">
        <iframe width="853" height="480" :src="videoLink" frameborder="0" allowfullscreen></iframe>
      </div>
    </div>
    <div class="row">
      <div v-html="article"></div>
    </div>
  </div>
</template>

<script>
  import * as services from '../services';
  export default {
    name: 'review',
    data () {
      return {
        topic: this.$route.path.split('/')[2],
        id: this.$route.params.id,
        title: '',
        article: '',
        author: '',
        videoLink: '',
        mainImage: '',
        error: ''
      }
    },
    created() {
      this.loadReview();
    },
    methods: {
      getTopic() {
        this.topic = this.$route.path.split('/')[2];
      },
      loadReview() {
        if (this.id) {
          services.reviewService.find({
            query: {
              _id: this.id
            }
          })
            .then(result => {
              let review = result.data[0];
              this.title = review.title;
              this.article = review.article;
              this.mainImage = review.mainImage;
              this.videoLink = review.videoLink;
            })
            .catch(err => {
              this.error = err;
            });
        }
      },
      getPic(value) {
        let imgs = require.context('../assets/', false, /\.jpg|gif|png$/);
        return imgs('./' + value);
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }
  li {
    display: inline-block;
    margin: 0 10px;
  }
  a {
    color: #42b983;
  }
  .topic {
    min-height: 800px;
  }
  .card-image .card-title a {
    color: #fff;
    font-weight: bolder;
  }
</style>



