<template>
  <div class="signup">
    <h2>
      Sign up for UGR!
    </h2>
    <h4>{{successMessage}}</h4>
    <form class="col s12">
      <div class="input-field">
        <v-icon prefix>account_circle</v-icon>
        <v-text-input name="email" v-model="email" id="email" class="validate" type="email"></v-text-input>
        <label for="email" data-error="Invalid Email Address" data-success="Valid!">
          Email Address
        </label>

      </div>
      <div class="input-field">
        <v-icon prefix>https</v-icon>
        <v-text-input name="password" v-model="password" id="password" type="password" class="validate"></v-text-input>
        <label for="password" data-error="Invalid" data-success="Valid!">
          Password
        </label>
      </div>
      <div class="input-field">
        <v-icon prefix>perm_identity</v-icon>
        <v-text-input name="firstName" v-model="firstName" id="firstName" class="validate"></v-text-input>
        <label for="firstName" data-error="You must provide a first name" data-success="Valid!">
          First Name
        </label>
      </div>
      <div class="input-field">
        <v-icon prefix>perm_identity</v-icon>
        <v-text-input name="lastName" v-model="lastName" id="lastName" class="validate"></v-text-input>
        <label for="lastName" data-error="You must provide a last name" data-success="Valid!">
          Last Name
        </label>
      </div>
      <div class="row">
        <v-btn large waves-effect><span v-on:click="signup">Signup</span></v-btn>
      </div>
    </form>
  </div>
</template>

<script>
  import * as services from '../services'
  export default {
    name: 'signup',
    data () {
      return {
        email: '',
        password: '',
        firstName: '',
        lastName: '',
        successMessage: ''
      }
    },
    methods: {
      validPass(val, event) {
        console.log('Val', val);
        console.log('Event', event);
        if (!val || val.length < 8) {
          return false;
        }
      },
      signup() {
        console.log('Form data', this.email, this.password, this.firstName, this.lastName);
        if (!this.email || !this.password || !this.firstName || !this.lastName) {
          window.Materialize.toast('You must enter all information', 4000);
        } else {
          services.userService.create({
            firstName: this.firstName,
            lastName: this.lastName,
            email: this.email,
            password: this.password
          })
            .then(this.email = '')
            .then(this.password = '')
            .then(this.firstName = '')
            .then(this.lastName = '')
            .then(this.successMessage = 'Thank you for registering with UGR!')
            .catch(function(err) {
              window.Materialize.toast(err, 4000);
            })
        }
      }
    }
  }
  window.jQuery(document).ready(function() {
    window.jQuery.validator.setDefaults({
      debug: true
    });
    window.jQuery('form').validate({
      rules: {
        firstName: 'required',
        lastName: 'required',
        email: {
          validate_email: true,
          required: true
        },
        password: {
          required: true,
          minlength: 8
        }
      }
    });
  });
</script>

<style>
  .signup {
    min-height: 800px;
    padding: 50px;
  }
  h2 {
    margin-bottom: 4rem !important;
  }
</style>



