<template>
  <div class="topic">
    <h1><span :class="'icon-' + topic.toLowerCase()"></span>{{ topic | capitalize }}</h1>
    <v-reviews :topic="topic"></v-reviews>
  </div>
</template>

<script>
  import ReviewView from './ReviewView.vue';
  export default {
    name: 'topic',
    components: {
      'v-reviews': ReviewView
    },
    data () {
      return {
        topic: this.$route.path.split('/')[2],
        link: this.$route.path
      }
    },
    watch: {
      '$route' (to, from) {
        this.getTopic();
      }
    },
    methods: {
      getTopic() {
        this.topic = this.$route.path.split('/')[2];
        this.reviews = [];
        console.log(this.topic)
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1, h2 {
    font-weight: normal;
  }
  ul {
    list-style-type: none;
    padding: 0;
  }
  li {
    display: inline-block;
    margin: 0 10px;
  }
  a {
    color: #42b983;
  }
  .topic {
    min-height: 800px;
  }
  .card-image .card-title a {
    color: #fff;
    font-weight: bolder;
  }
</style>



